.container{height:100%;display:flex}.quiz-wrapper{display:flex;justify-content:center;flex:0.5 1}

.main-question-section{background-image:url(/quiz-interaction/static/media/balloon-background-image.65837853.png),linear-gradient(rgba(0,0,0,0.5), rgba(0,0,0,0.5));flex:0.5 1;background-size:cover;background-blend-mode:overlay}.main-question-section .main-question-section-body{color:#fff;padding:4em 5em 0 8em;margin-left:auto;-webkit-animation:slide-up 0.8s;animation:slide-up 0.8s;transition-timing-function:ease-in}.main-question-section .main-question-section-body .main-question-text{font-size:42px;font-weight:500}.main-question-section .main-question-section-body .main-question-subtitle{font-size:18px;font-weight:300;color:#EBE9F3}.main-question-section .main-question-section-body .footer-wrapper{margin-top:10em}.main-question-section .main-question-section-body .footer-wrapper .inventor{font-size:14px;font-weight:500}.main-question-section .main-question-section-body .footer-wrapper .date{margin-top:1em;font-size:12px;font-weight:300}@-webkit-keyframes slide-up{from{margin-top:30px;opacity:0.2}to{margin-top:0px;opacity:1}}@keyframes slide-up{from{margin-top:30px;opacity:0.2}to{margin-top:0px;opacity:1}}

.header-section{width:100%;padding-top:3em;display:flex;justify-content:space-between;align-items:center;max-width:512px;color:#fff}.header-section .logo-wrapper{display:flex;align-items:center;margin-left:8em}.header-section .logo-wrapper:hover{color:#aaa}.header-section .logo-wrapper h3{margin-left:10px}.header-section .social-icons-wrapper{width:10em;display:flex;justify-content:space-evenly}.header-section .social-icons-wrapper .social-icon{color:#fff}.header-section .social-icons-wrapper .social-icon:hover{color:#aaa}

.progress-bar{height:0.2em;background-color:#B7C1CA;opacity:0.5}

.quiz-section{padding:8em 3em 10em 5em;box-sizing:border-box;overflow-y:scroll}.quiz-section::-webkit-scrollbar{display:none}.appear-animate{-webkit-animation:slide-up 2s !important;animation:slide-up 2s !important;transition-timing-function:ease-in !important}@-webkit-keyframes slide-up{from{margin-top:30px;opacity:0.2}to{margin-top:0px;opacity:1}}@keyframes slide-up{from{margin-top:30px;opacity:0.2}to{margin-top:0px;opacity:1}}

.question-card-container{height:60vh;margin-bottom:5em;overflow-y:hidden}.question-card-container .question-label{color:#02E746;font-size:16px;font-weight:500;margin-bottom:10px}.question-card-container .question-text{font-size:32px;font-weight:500;margin-bottom:0.5em}.question-card-container .answer-section{display:flex;flex-direction:column}.question-card-container .answer-section button.option-btn{padding:1em;background-color:#EBE9F3;margin-top:10px;border-radius:5px;border:none;text-align:left !important}.question-card-container .answer-section button.option-btn:focus{outline:none}.question-card-container .answer-section button.selected-option{padding:1em;background-color:#74b9ff;margin-top:10px;border-radius:5px;border:none;text-align:left !important}.question-card-container .answer-section button.selected-option:focus{outline:none}.question-card-container .answer-section button.submit-btn{padding:1.5em;background-color:#02E746;border-radius:5px;margin-top:10px;border:none}.question-card-container .answer-section button.submit-btn:focus{outline:none}.question-card-container .answer-section button.submit-btn:hover{background-color:#2ecc71}button:disabled{background-color:#778298 !important;color:#fff}@-webkit-keyframes slide-up{from{margin-top:30px;opacity:0.2}to{margin-top:0px;opacity:1}}@keyframes slide-up{from{margin-top:30px;opacity:0.2}to{margin-top:0px;opacity:1}}

.result-container{padding:0 8em;text-align:center;color:#545565;-webkit-animation:slide-up 0.8s;animation:slide-up 0.8s;transition-timing-function:ease-in}.result-container img{width:60px;height:auto}.result-container .aggregate-value{font-size:100px;font-weight:700}.result-container .message{font-size:30px;font-weight:500}.result-container .success-ratio{font-size:14px;font-weight:normal;margin-top:1em}.result-container .Retry{padding:1em 5em;background-color:#02E746;margin-top:1em;border:none}.result-container .Retry:focus{outline:none}.result-container .Retry:hover{background-color:#2ecc71}@-webkit-keyframes slide-up{from{margin-top:30px;opacity:0.2}to{margin-top:0px;opacity:1}}@keyframes slide-up{from{margin-top:30px;opacity:0.2}to{margin-top:0px;opacity:1}}

body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}::-webkit-scrollbar{display:none}code{font-family:source-code-pro, Melno, Monaco, Consolas, 'Courier New', monospace}body>#root>div{height:100vh}.cursor{width:20px;height:20px;border:2px solid #2ed573;border-radius:50%;position:absolute;pointer-events:none}.cursor::after{content:"";width:20px;height:20px;position:absolute;border:8px solid #2ed573;border-radius:50%;opacity:.2;top:-8px;left:-8px;-webkit-animation:cursorAnim2 .5s infinite alternate;animation:cursorAnim2 .5s infinite alternate}@-webkit-keyframes cursorAnim{from{transform:scale(1)}to{transform:scale(0.7)}}@keyframes cursorAnim{from{transform:scale(1)}to{transform:scale(0.7)}}@-webkit-keyframes cursorAnim2{from{transform:scale(1)}to{transform:scale(0.4)}}@keyframes cursorAnim2{from{transform:scale(1)}to{transform:scale(0.4)}}@-webkit-keyframes cursorAnim3{0%{transform:scale(1)}50%{transform:scale(2.5)}100%{transform:scale(1);opacity:0}}@keyframes cursorAnim3{0%{transform:scale(1)}50%{transform:scale(2.5)}100%{transform:scale(1);opacity:0}}.expand{-webkit-animation:cursorAnim3 .5s forwards;animation:cursorAnim3 .5s forwards;border:2px solid #2ed573}

